.container {
  width: 280px;
  margin: 0 auto;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensans.woff");
  src: url("../fonts/opensans.woff2");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensansbold.woff");
  src: url("../fonts/opensansbold.woff2");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensanslight.woff");
  src: url("../fonts/opensanslight.woff2");
  font-style: normal;
  font-weight: 300;
}
body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  width: 320px;
  font-family: "OpenSans", "Arial", "sans-serif";
  /*background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 259px;*/
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
a {
  text-decoration: none;
}
@media (min-width: 660px) {
  body {
    width: 660px;
  }
  .container {
    width: 620px;
    margin: 0 auto;
  }
}
.main-nav {
  font-size: 18px;
  line-height: 32px;
  background-color: rgba(0, 0, 0, 0.3);
  color: #ffffff;
}
.page-header {
  background-color: #283645;
  /*background-image: url(../../img/bg-back-mobile@1x.jpg);
  background-position-y: 259px;
  background-size: 100% auto;
  background-repeat: no-repeat;*/
  color: #ffffff;
  padding-top: 22px;
}
.page-header .page-header__wrapper {
  display: flex;
  justify-content: space-between;
  padding-bottom: 22px;
}
.menu .menu__list {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  margin: 0;
}
.menu .menu__list .menu__item {
  text-align: center;
  padding: 22px 0;
  background-color: #283645;
  box-shadow: 0 1px 0 0 #000000, inset 0 1px 0 0 rgba(255, 255, 255, 0.15);
}
.menu .menu__list .menu__link {
  display: block;
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  text-transform: uppercase;
  width: 100%;
}
.app {
  padding-top: 110px;
  background-image: url("../../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 -66px;
}
.app .app__wrapper .app__picture {
  margin: 0;
}
.app .app__wrapper .app__picture::after {
  display: block;
  content: "";
  background-image: url("../../img/iphone-hand-mobile@1x.png");
  background-repeat: no-repeat;
  width: 258px;
  height: 286px;
}
.app .app__wrapper .app__image {
  vertical-align: bottom;
}
.app .app__wrapper .app__container {
  background-color: #1d2631;
}
.app__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.app__list .app__item .app__link {
  color: #ffffff;
}
.app__description {
  color: #ffffff;
  margin: 0;
}
@media (min-width: 320px) {
  .app__title {
    display: none;
    color: #ffffff;
  }
}
@media (min-width: 660px) {
  .app {
    background-image: url(../../img/bg-back-tablet@1x.jpg);
  }
  .app .app__title {
    display: block;
  }
}
